body, html { font-family: 'Lato', sans-serif; position: relative; font-size: 14px; line-height: 1.4; -webkit-text-size-adjust: none; }

img { max-width: 100%; }

.wrapper { max-width: 1430px; padding: 0 15px; margin: 0 auto; }

.title { font-size: 30px; margin-bottom: 35px; text-align: center; }

.h2 { position: relative; display: inline-block; font-family: 'Raleway', sans-serif; font-size: 28px; font-weight: bold; color: #1ab798; }

.h2:before { content: ''; position: absolute; width: 60px; height: 1px; background: #babdc1; left: -72px; top: 50%; transform: translateY(-50%); }

.h2:after { content: ''; position: absolute; width: 60px; height: 1px; background: #babdc1; right: -72px; top: 50%; transform: translateY(-50%); }

.s-line { font-size: 0; width: 170px; margin: 0 auto; margin-top: 8px; }

.s-line span { position: relative; display: inline-block; width: 78px; height: 2px; background: #343c3f; }

.s-line__left { margin-right: 2px; }

.s-line__left:after { content: ''; position: absolute; width: 11px; height: 2px; transform: rotate(122deg); background: #343c3f; right: -7px; top: -4px; }

.s-line__right { margin-left: 2px; }

.s-line__right:before { content: ''; position: absolute; width: 11px; height: 2px; transform: rotate(122deg); background: #343c3f; left: -7px; top: 4px; }

.btn { font-size: 16px; color: #fff; padding: 5px 15px; background-color: #c0af96; border: 2px solid #c0af96; }

.btn-form { max-width: 300px; width: 100%; }

.btn:hover { color: #c0af96; background-color: #fff; }

.btn:focus { box-shadow: none; }

.progress-container { position: absolute; bottom: 0; left: 0; width: 100%; height: 3px; margin-bottom: 0px; overflow: hidden; background-color: transparent; z-index: 1500; }

.progress-bar { width: 0%; float: left; height: 100%; z-index: 99; max-width: 100%; background-color: #fc3865; transition: width .6s ease; }

.scroll-down { position: absolute; bottom: 1%; left: 50%; transform: translate(-50%, 0); }

.scroll-down__img { position: relative; top: 0; display: inline-block; animation: bounce .3s ease infinite alternate; }

@keyframes bounce { 100% { top: -20px; } }

.s-price__span { font-weight: bold; }

.m-0 { margin: 0; }

.paragraph-scroll { position: absolute; top: -50px; width: 100%; height: 1px; }

.header { position: relative; min-height: 100vh; background-image: url(../img/header-section-bg.jpg); background-size: cover; background-position: center center; background-attachment: fixed; }

.topline { position: fixed; padding: 10px 15px; right: 0; left: 0; background-color: #fff; box-shadow: 0 -3px 33px 0px #362424; z-index: 9999; }

.topline__content { display: flex; justify-content: space-between; align-items: center; max-width: 1000px; margin: 0 auto; }

.topline__right { display: flex; align-items: center; }

.main-nav { display: flex; align-items: center; justify-content: center; padding: 10px 0; }

.main-nav__item { margin: 0 20px; padding: 10px 0; }

.main-nav__link { position: relative; display: block; font-size: 16px; color: #000; text-decoration: none; }

.main-nav__link:before { content: ''; position: absolute; bottom: -4px; width: 0; left: 50%; height: 1px; background: #ab9256; transform: translateX(-50%); transition: all .3s; }

.main-nav__link:hover { color: #000; text-decoration: none; }

.main-nav__link:hover:before { width: 100%; }

.logo { width: 100px; }

.mobile-menu-btn { position: absolute; }

.about { position: relative; }

.about__content { padding: 80px 0; }

.about__descr { display: flex; justify-content: space-between; flex-wrap: wrap; }

.about__block { flex: 0 0 30%; margin: 35px 0; }

.about-item { display: flex; }

.about-item__content { flex: 1 0 calc(100% - 30px); padding-left: 20px; }

.about-item__icon { flex: 1 0 30px; }

.about-item__title { font-size: 18px; font-weight: bold; text-transform: uppercase; }

.about-item__text { font-size: 14px; max-width: 300px; }

.courses { position: relative; background: #f8f8f8; background-image: url(../img/bg-1.jpg); background-size: cover; background-position: center center; background-attachment: fixed; }

.courses__content { padding: 80px 35px; background-color: rgba(255, 255, 255, 0.9); }

.courses__descr { max-width: 900px; margin: 0 auto; }

.courses-item { display: flex; margin-bottom: 50px; }

.courses-item__inner { display: flex; }

.courses-item__img { flex: 0 0 240px; }

.courses-item__icon { flex: 0 0 32px; }

.courses-item__title { font-size: 22px; margin-bottom: 15px; font-weight: bold; }

.courses-item__subtitle { font-size: 18px; margin-bottom: 15px; }

.courses-item__descr { flex: 0 0 calc(100% - 240px); padding-left: 40px; }

.courses-item__text { font-size: 16px; padding-top: 6px; }

.courses-item_right .courses-item__descr { padding: 0 0 0 40px; }

.auth-courses { position: relative; }

.auth-courses__content { padding: 80px 0; }

.services { position: relative; background-image: url(../img/services/services-bg.jpeg); background-size: cover; background-position: center center; background-attachment: fixed; overflow-x: hidden; }

.services__content { padding: 80px 35px; background-color: rgba(255, 255, 255, 0.9); }

.services__descr { display: flex; justify-content: space-between; flex-wrap: wrap; padding-top: 45px; }

.services-item { width: 45%; margin-bottom: 65px; }

.services-item__top { display: flex; align-items: center; justify-content: space-between; }

.services-item__img { width: 200px; }

.services-item__title { font-size: 20px; padding: 0 30px 0 20px; }

.services-item__descr { padding: 10px 0 20px 0; }

.services-item__text { font-size: 16px; }

.feedback { position: relative; padding: 80px 0; }

.order-form { max-width: 800px; margin: 0 auto; }

.order-form__inner { display: flex; justify-content: space-between; margin-bottom: 35px; }

.order-form__field { position: relative; z-index: 1; display: inline-block; width: 350px; vertical-align: top; }

.order-form__field-big { width: 100%; }

.order-form__btn { text-align: center; }

.order-form__textarea { font-size: 14px; height: 200px; width: 100%; color: #5198fc; padding: 10px; border: 1px solid #5198fc; resize: none; }

.order-form__textarea::placeholder { color: #5198fc; }

.order-form__input { position: relative; display: block; float: right; padding: 0.8em; border: none; border-radius: 0; background: #f0f0f0; color: #aaa; width: 100%; background: transparent; color: #5198FC; font-weight: bold; font-family: "Helvetica Neue", Helvetica, Arial, sans-serif; -webkit-appearance: none; /* for box shadows to show on iOS */ }

.order-form__input:focus + .order-form__label .graphic--madoka { stroke-dashoffset: 0; }

.order-form__input:focus + .order-form__label .order-form__label-content { transform: scale3d(0.81, 0.81, 1) translate3d(0, -40px, 0); }

.order-form__input:focus + .order-form__label { cursor: default; pointer-events: none; }

.order-form__label { display: flex; align-items: center; font-size: 14px; padding: 0 10px; color: #6a7989; position: absolute; width: 100%; height: 100%; color: #5198FC; cursor: text; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; }

.order-form__label-content { position: relative; display: block; width: 100%; transform-origin: 0% 50%; transition: transform 0.3s; }

.graphic { position: absolute; top: 0; left: 0; fill: none; }

.graphic--madoka { transform: scale3d(1, -1, 1); transition: stroke-dashoffset 0.3s; pointer-events: none; stroke: #5198FC; stroke-width: 4px; stroke-dasharray: 962; stroke-dashoffset: 558; }

.input--filled .input__label--madoka { cursor: default; pointer-events: none; }

.input--filled .graphic--madoka { stroke-dashoffset: 0; }

.input--filled .order-form__label-content { transform: scale3d(0.81, 0.81, 1) translate3d(0, -40px, 0); }

.slide-item__text { font-family: "LSTKDayfly-Prototype"; font-size: calc(20px + (64 - 20) * ((100vw - 320px) / (1920 - 320))); color: #fff; margin-right: 30px; padding: 0; transform: rotate(-360deg) scale(0.15); opacity: 0; color: #000; transition: all 2s; line-height: 1.8; }

.slide-item__img { display: flex; align-items: center; transition: all 2s; opacity: 0; transform: scale(0.15); }

.slide-item__content { display: flex; justify-content: space-around; align-items: center; margin: 0 20px; padding-top: 10%; padding-bottom: 20%; }

.hero { min-height: 100vh; display: flex; align-items: center; padding-top: 150px; }

.hero__content { display: flex; }

.hero__left { width: 60%; }

.hero__right { display: flex; justify-content: center; width: 40%; }

.hero__title { font-size: 25px; margin-bottom: 30px; }

.hero__text { font-size: 18px; margin-bottom: 30px; }

.cube-block { text-align: center; position: relative; width: 100%; height: 100%; transform: rotateX(-15deg) rotateY(15deg); transform-style: preserve-3d; animation: rotateCube 7s infinite linear; transform-style: preserve-3d; transform: rotateX(-15deg) rotateY(15deg); animation: rotateCube 7s infinite linear; -webkit-transform-style: preserve-3d; -webkit-transform: rotateX(-15deg) rotateY(15deg); -webkit-animation: rotateCube 7s infinite linear; }

.cube-wrapper { perspective: 1000px; -webkit-perspective: 1000px; width: 250px; height: 250px; text-align: center; display: inline-block; }

.cube { position: absolute; /*border: 1px solid #fff;*/ background: #FFF; opacity: .85; background-size: auto 100%; }

.cube-front { width: 250px; height: 250px; -webkit-transform: translate3d(0, 0, 125px); transform: translate3d(0, 0, 125px); background-color: #000; }

.cube-back { width: 250px; height: 250px; -webkit-transform: rotateY(180deg) translate3d(0, 0, 125px); transform: rotateY(180deg) translate3d(0, 0, 125px); background-color: #000; }

.cube-top { width: 250px; height: 250px; -webkit-transform: rotateX(90deg) translate3d(0, 0, 125px); transform: rotateX(90deg) translate3d(0, 0, 125px); background-color: #000; }

.cube-bottom { width: 250px; height: 250px; -webkit-transform: rotateX(-90deg) translate3d(0, 0, 125px); transform: rotateX(-90deg) translate3d(0, 0, 125px); background-color: #000; }

.cube-left { width: 250px; height: 250px; left: 50%; margin-left: -125px; -webkit-transform: rotateY(-90deg) translate3d(0, 0, 125px); transform: rotateY(-90deg) translate3d(0, 0, 125px); background-color: #000; }

.cube-right { width: 250px; height: 250px; left: 50%; margin-left: -125px; -webkit-transform: rotateY(90deg) translate3d(0, 0, 125px); transform: rotateY(90deg) translate3d(0, 0, 125px); background-color: #000; }

@-webkit-keyframes rotateCube { 0% { -webkit-transform: rotateY(0deg) rotateX(0deg); }
  100% { -webkit-transform: rotateY(-360deg) rotateX(-360deg); } }

@keyframes rotateCube { 0% { transform: rotateY(0deg) rotateX(0deg); }
  100% { transform: rotateY(-360deg) rotateX(-360deg); } }

.footer { padding: 50px 0; background-color: #c1c1c1; background-image: url(../img/footer-bg.jpg); background-size: cover; background-position: center center; }

.footer__content { padding: 50px 0; text-align: center; color: #fff; }

.footer__title { font-size: 18px; margin-bottom: 15px; }

.copyright { text-align: center; }

.copyright__text { color: #fff; }

.f-social { padding: 38px 0 0 0; }

.f-social__item { display: inline-block; margin-right: 13px; }

.f-social__link { position: relative; display: block; width: 43px; height: 43px; border-radius: 50%; transition: transform 0.3s; }

.f-social__link svg { position: absolute; width: 1em; height: 1em; top: 0; right: 0; bottom: 0; left: 0; margin: auto; font-size: 23px; fill: #fff; }

.f-social__link:hover { transform: rotate(360deg) scale(1.15); }

.f-tw-icon { background: #3b5997; }

.f-fb-icon { background: #3ec2f3; }

.f-g-icon { background: #e44c41; }

.slick-active .rotateIn { transform: rotate(0deg) scale(1); opacity: 1; transition: all 2s; }

.reset-animation .slide-item__text { transform: rotate(-360deg) scale(0); transition: all 0s; }

.slick-active .zoomIn { opacity: 1; transform: scale(1); }

.animated { -webkit-animation-duration: 1s; -moz-animation-duration: 1s; -o-animation-duration: 1s; animation-duration: 1s; -webkit-animation-fill-mode: both; -moz-animation-fill-mode: both; -o-animation-fill-mode: both; animation-fill-mode: both; }

@-moz-keyframes bounceInLeft { 0% { opacity: 0;
    -moz-transform: translateX(-2000px); }
  60% { opacity: 1;
    -moz-transform: translateX(30px); }
  80% { -moz-transform: translateX(-10px); }
  100% { -moz-transform: translateX(0); } }

@-o-keyframes bounceInLeft { 0% { opacity: 0;
    -o-transform: translateX(-2000px); }
  60% { opacity: 1;
    -o-transform: translateX(30px); }
  80% { -o-transform: translateX(-10px); }
  100% { -o-transform: translateX(0); } }

@keyframes bounceInLeft { 0% { opacity: 0;
    transform: translateX(-2000px); }
  60% { opacity: 1;
    transform: translateX(30px); }
  80% { transform: translateX(-10px); }
  100% { transform: translateX(0); } }

.hidden { opacity: 0; }

.animated.bounceInLeft { -webkit-animation-name: bounceInLeft; -moz-animation-name: bounceInLeft; -o-animation-name: bounceInLeft; animation-name: bounceInLeft; }

@-webkit-keyframes bounceInRight { 0% { opacity: 0;
    -webkit-transform: translateX(2000px); }
  60% { opacity: 1;
    -webkit-transform: translateX(-30px); }
  80% { -webkit-transform: translateX(10px); }
  100% { -webkit-transform: translateX(0); } }

@-moz-keyframes bounceInRight { 0% { opacity: 0;
    -moz-transform: translateX(2000px); }
  60% { opacity: 1;
    -moz-transform: translateX(-30px); }
  80% { -moz-transform: translateX(10px); }
  100% { -moz-transform: translateX(0); } }

@-o-keyframes bounceInRight { 0% { opacity: 0;
    -o-transform: translateX(2000px); }
  60% { opacity: 1;
    -o-transform: translateX(-30px); }
  80% { -o-transform: translateX(10px); }
  100% { -o-transform: translateX(0); } }

@keyframes bounceInRight { 0% { opacity: 0;
    transform: translateX(2000px); }
  60% { opacity: 1;
    transform: translateX(-30px); }
  80% { transform: translateX(10px); }
  100% { transform: translateX(0); } }

.animated.bounceInRight { -webkit-animation-name: bounceInRight; -moz-animation-name: bounceInRight; -o-animation-name: bounceInRight; animation-name: bounceInRight; }
